@use "@material/density/variables" as density-variables;
@use "@material/theme/variables" as theme-variables;

$track-width: 32px !default;
$track-height: 14px !default;
$thumb-diameter: 20px !default;
$ripple-size: 48px !default;

$minimum-size: 28px !default;
$maximum-size: $ripple-size !default;
$density-scale: density-variables.$default-scale !default;
$density-config: (
  size: (
    minimum: $minimum-size,
    default: $maximum-size,
    maximum: $maximum-size,
  ),
) !default;

// Amount the edge of the thumb should be offset from the edge of the track.
$thumb-offset: 4px !default;

$thumb-active-margin:
  $track-width - $thumb-diameter + $thumb-offset * 2 !default;

$toggled-off-thumb-color: theme-variables.prop-value(surface) !default;
$toggled-off-track-color: theme-variables.prop-value(on-surface) !default;
$toggled-off-ripple-color: #9e9e9e !default;
$disabled-thumb-color: theme-variables.prop-value(surface) !default;
$disabled-track-color: theme-variables.prop-value(on-surface) !default;

$baseline-theme-color: secondary !default;
